    // 【肌肉率】=(肌肉重量/体重kg)×100% 正常区间    40% <= x <= 60%
    /*
     偏低建议：您体内的肌肉含量偏低，请您加强锻炼。
     正常建议：您体内的肌肉含量标准，身体匀称健康，请您保持锻炼。
     偏高建议：您体内的肌肉含量优秀，身体强壮健康，请您保持锻炼。
     */
    // hi:体重KG ,mus:肌肉
    muscleReport:function (hi,mus){

      var hid = parseFloat(hi);
      var musd = parseFloat(mus);

      var value = (musd / hid) * 100 ;

      var count1 = 40;
      var count2 = 60;

      var backString = "";
      var status = "";

      if( value < count1 ){
        backString = "您体内的肌肉含量偏低，请您加强锻炼。";
        status = "偏低";
      }else if( count1 <= value && value <= count2 ){
        backString = "您体内的肌肉含量标准，身体匀称健康，请您保持锻炼。";
        status = "正常";
      }else if( value > count2 ){
        backString = "您体内的肌肉含量优秀，身体强壮健康，请您保持锻炼。";
        status = "偏高";
      }

      var backObject = new Object();
      backObject.report = backString;
      backObject.status = status;

      return backObject;

    },

    // 【骨量】 正常区间 3.1 <= x <= 3.3
    /*
     偏低建议：您的骨量偏低，请加强营养和锻炼；在“骨银行”中的“储蓄”越多，可供日后消耗的骨量就越多。
     标准建议：您的骨量标准，请继续保持；在“骨银行”中的“储蓄”越多，可供日后消耗的骨量就越多。
     优秀建议：您的骨量丰富，请继续保持；在“骨银行”中的“储蓄”越多，可供日后消耗的骨量就越多。
     */
