vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
judgeLiveTime(){
if(this.info.permanentAddress<3){
this.toastMsg = '本市户籍，无需填写';
this.showToast = true;
this.isLiveTime = true;
}
},
changeRadioOption(value, label){
console.log(value, label)
if(value==3){
this.showRadioHistoryOther = true;
}else{
this.showRadioHistoryOther = false;
}
this.tempDialogRadioKey = value;
},
cancelDialog3(){
this.diseaseHistory = [];
this.diseaseHistoryType = '';
this.tempDialogRadioKey = '';
this.radioHistoryOther = '';
this.showDialog3 = false;
},
successDialog3(){
let obj = {itemId:3,itemName:'',itemType:37};
if(this.tempDialogRadioKey==3){
obj.itemName = this.radioHistoryOther;
}else{
obj.itemName = this.dictionaries.medicineType[this.tempDialogRadioKey];
}
this.info[this.tempDialogCheckKey] = obj;
this.medicineTypeText = obj.itemName;
this.diseaseHistory = [];
this.diseaseHistoryType = '';
this.tempDialogRadioKey = '';
this.radioHistoryOther = '';
this.showDialog3 = false;
}
}
}



